*{
  font-family: Helvetica, Arial, sans-serif;
}
body{
  padding:60px;
}
 input{
   color: grey;
   font-size: 1.2rem;
   width: 100%;

 }

.card{
  align-content: center;
  border: 1px solid grey;
  margin-bottom: 10px;
  margin-right:10px;
  overflow: hidden;
  p{
    padding:5px;
    text-align: center;
  }
  img{
    max-height: 170px;
  }
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
